import React from "react";
import Card from "react-bootstrap/Card";
import ListGroup from "react-bootstrap/ListGroup";

const QuestionScore = ({ question, scoreArray }) => {
  question.question = question.question.replace(/&quot;/g, '"');
  question.question = question.question.replace(/&#039;/g, "'");

  //multiple choice score check
  if (question.type === "multiple") {
    if (scoreArray[question.id].correct === false) {
      return (
        <div>
          <Card>
            <Card.Header>
              <strong>{question.category}</strong>
            </Card.Header>
            <Card.Body>
              <Card.Text>{question.question}</Card.Text>
              <ListGroup>
                {question.incorrect_answers.map((ans) => (
                  <>
                    {ans === question.correct_answer && (
                      <ListGroup.Item variant="success">
                        {ans.replace(/&#039;/g, "'")}
                      </ListGroup.Item>
                    )}
                    {ans === scoreArray[question.id].ans && (
                      <ListGroup.Item variant="danger">
                        {ans.replace(/&#039;/g, "'")}
                      </ListGroup.Item>
                    )}
                    {ans !== scoreArray[question.id].ans &&
                      ans !== question.correct_answer && (
                        <ListGroup.Item>
                          {ans.replace(/&#039;/g, "'")}
                        </ListGroup.Item>
                      )}
                  </>
                ))}
              </ListGroup>
            </Card.Body>
          </Card>
        </div>
      );
    } else {
      return (
        <div>
          <Card>
            <Card.Header>
              <strong>{question.category}</strong>
            </Card.Header>
            <Card.Body>
              <Card.Text>{question.question}</Card.Text>
              <ListGroup>
                {question.incorrect_answers.map((ans) =>
                  ans === question.correct_answer ? (
                    <ListGroup.Item variant="success">
                      {ans.replace(/&#039;/g, "'")}
                    </ListGroup.Item>
                  ) : (
                    <ListGroup.Item>
                      {ans.replace(/&#039;/g, "'")}
                    </ListGroup.Item>
                  )
                )}
              </ListGroup>
            </Card.Body>
          </Card>
        </div>
      );
    }
  }

  //bool score check
  if (question.type === "boolean") {
    if (question.correct_answer === true) {
      return (
        <div>
          <Card>
            <Card.Header>
              <strong>{question.category}</strong>
            </Card.Header>
            <Card.Body>
              <ListGroup>
                <ListGroup.Item>True</ListGroup.Item>
                <ListGroup.Item>False</ListGroup.Item>
              </ListGroup>
            </Card.Body>
          </Card>
        </div>
      );
    } else {
      return (
        <div>
          <Card>
            <Card.Header>
              <strong>{question.category}</strong>
            </Card.Header>
            <Card.Body>
              <ListGroup>
                <ListGroup.Item>True</ListGroup.Item>
                <ListGroup.Item>False</ListGroup.Item>
              </ListGroup>
            </Card.Body>
          </Card>
        </div>
      );
    }
  } else if (question.type === "typed") {
  }
};

const ShowScore = ({ score, name, time, scoreArray, quiz }) => {
  if (score === null) {
    return <div></div>;
  } else {
    return (
      <div>
        <Card>
          <Card.Header>
            Congratulations, {name}! You got {score} correct in {time} seconds!
          </Card.Header>

          <Card.Body>
            {quiz.map((question) => (
              <QuestionScore
                id={question.id}
                question={question}
                scoreArray={scoreArray}
              />
            ))}
          </Card.Body>
        </Card>
      </div>
    );
  }
};

export default ShowScore;
