import { useState, useEffect } from "react";
import axios from "axios";
import React from "react";

import QuestionForm from "./components/QuizForm";
import ShowScore from "./components/ShowScore";
import Card from "react-bootstrap/Card";
import "bootstrap/dist/css/bootstrap.min.css";

const days = [
  "sunday",
  "monday",
  "tuesday",
  "wednesday",
  "thursday",
  "friday",
  "saturday",
];

const day = new Date();
//Extra question for text input
const extraQuestion = {
  id: 10,
  type: "typed",
  question: "What day is today?",
  answers: false,
  correct_answer: days[day.getDay()],
};

const App = () => {
  //User answers
  const [allAnswers, setAllAnswers] = useState(Array.apply("", Array(11)));
  //Final score
  const [score, setScore] = useState(null);
  //The quiz objects array are stored here
  const [quiz, setQuiz] = useState([]);
  //quiz timer
  const [timer, setTimer] = useState(0);
  //Name of user
  const [name, setName] = useState(null);

  //bool value for whether the user has submitted, necessary for conditional render
  const [hasSubmitted, setHasSubmitted] = useState(false);
  //Stores the user's final answer, the correct answer, and whether the answer was correct
  const [scoreArray, setScoreArray] = useState([]);
  //Final time
  const [finalTime, setFinalTime] = useState(0);
  //Get 10 random questions and store it, from api
  const hook = () => {
    axios
      .get("https://opentdb.com/api.php?amount=10&difficulty=easy")
      .then((res) => {
        //Assign id to each question
        res.data.results.forEach((o, i) => {
          o.id = i;
        });

        //store questions in variable and push extra question

        var tempQuiz = res.data.results;
        tempQuiz.push(extraQuestion);
        //set quiz to response values
        setQuiz(res.data.results);
      });
  };
  useEffect(hook, []);

  //timer for quiz
  setTimeout(() => setTimer(timer + 1), 1000);

  const submitAnswer = (event) => {
    event.preventDefault();
    const finalTime = timer;
    //final score array, checks whether the answer is correct
    const finalScoreArr = allAnswers.map((answer, index) => ({
      correct: answer === quiz[index].correct_answer,
      id: index,
      correct_ans: quiz[index].correct_answer,
      ans: answer,
    }));

    //submission occurred
    setHasSubmitted(true);
    //set the score array for later display
    setScoreArray(finalScoreArr);

    const finalScore = allAnswers.filter(
      (answer, index) => answer === quiz[index].correct_answer
    );

    const newResult = {
      username: name,
      score: finalScore.length,
      time: finalTime,
      date: new Date(),
    };

    setFinalTime(finalTime);
    setScore(finalScore.length);

    //post results to json
    axios.post("/api/results", newResult);
  };

  //Name input handler
  const handleName = (event) => {
    setName(event.target.value);
  };

  //Render quiz
  //Cards used throughout from bootstrap
  if (!hasSubmitted) {
    return (
      <div>
        <Card bg="light" className="justify-content-center">
          <Card.Header className="text-center">
            <h1>CS328 Objective quiz</h1>
            <div>Timer: {timer}</div>
          </Card.Header>
          <Card.Body>
            <QuestionForm
              quiz={quiz}
              submitAnswer={submitAnswer}
              allAnswers={allAnswers}
              setAllAnswers={setAllAnswers}
              name={name}
              handleName={handleName}
            />
          </Card.Body>
        </Card>
      </div>
    );
  }
  //Render scores
  else {
    return (
      <div>
        <Card body>
          <Card.Header className="text-center">
            <h1>CS328 Objective quiz</h1>
          </Card.Header>
          <ShowScore
            score={score}
            name={name}
            scoreArray={scoreArray}
            time={finalTime}
            quiz={quiz}
          />
        </Card>
      </div>
    );
  }
};

export default App;
