import React from "react";
import { useState, useEffect } from "react";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import ListGroup from "react-bootstrap/ListGroup";

const Question = ({ question, allAnswers, setAllAnswers }) => {
  //Each question has a current answer obj and state
  const [currentAnswer, setCurrentAnswer] = useState("");
  //answer handler
  const handleAnswer = (event) => {
    setCurrentAnswer(event.target.value);
    const currAnswer = event.target.value;
    //change the current answer in the full answer array
    const answersCopy = allAnswers.map((answer, index) =>
      index === question.id ? currAnswer : answer
    );
    setAllAnswers(answersCopy);
  };
  //random integer for multiple choice display
  const getRandomInt = (max) => {
    return Math.floor(Math.random() * max);
  };
  //replace string codes
  question.question = question.question.replace(/&quot;/g, '"');
  question.question = question.question.replace(/&#039;/g, "'");
  question.question = question.question.replace(/&amp;/g, "&");

  //determine the question type
  if (question.type === "boolean") {
    return (
      <div>
        <Card>
          <Card.Header>
            <strong>{question.category}</strong>
          </Card.Header>
          <Card.Body>
            <Card.Text>{question.question}</Card.Text>
            <ListGroup>
              <ListGroup.Item>
                {" "}
                <input
                  type="radio"
                  value="True"
                  onChange={handleAnswer}
                  checked={"True" === currentAnswer}
                />
                True
              </ListGroup.Item>
              <ListGroup.Item>
                <input
                  type="radio"
                  value="False"
                  onChange={handleAnswer}
                  checked={"False" === currentAnswer}
                />
                False
              </ListGroup.Item>
            </ListGroup>
          </Card.Body>
        </Card>
      </div>
    );
  }
  if (question.type === "multiple") {
    if (!question.incorrect_answers.includes(question.correct_answer)) {
      question.incorrect_answers.splice(
        getRandomInt(3),
        0,
        question.correct_answer
      );
    }
    return (
      <div>
        <Card>
          <Card.Header>
            <strong>{question.category}</strong>
          </Card.Header>
          <Card.Body>
            <Card.Text>{question.question}</Card.Text>
            <ListGroup>
              {question.incorrect_answers.map((q) => (
                <ListGroup.Item>
                  {" "}
                  <input
                    type="radio"
                    value={q}
                    onChange={handleAnswer}
                    checked={q === currentAnswer}
                  />
                  {q.replace(/&#039;/g, "'")}
                </ListGroup.Item>
              ))}
            </ListGroup>
          </Card.Body>
        </Card>
      </div>
    );
  }
  if (question.type === "typed") {
    return (
      <div>
        <Card body>
          <p>{question.question} </p>
          <input type="text" onChange={handleAnswer} />
        </Card>
      </div>
    );
  }
};

const QuizForm = ({
  quiz,
  submitAnswer,
  allAnswers,
  setAllAnswers,
  handleName,
}) => {
  return (
    <div>
      <form onSubmit={submitAnswer}>
        <p>What is your name? </p>
        <input type="text" onChange={handleName} required />
        <br />
        <Card>
          {quiz.map((question) => (
            <Question
              id={question.id}
              question={question}
              allAnswers={allAnswers}
              setAllAnswers={setAllAnswers}
            />
          ))}
        </Card>
        <Button variant="primary" type="submit">
          Submit
        </Button>
      </form>
    </div>
  );
};

export default QuizForm;
